.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 32px 19px;
  display: block;
}

.card__name {
  color: #060b35;
  padding: 8px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  padding-left: 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__stars {
  justify-content: space-between;
  padding: 17px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding: 6px 16px 0;
  display: flex;
}

.card__price div:first-child {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price div:last-child {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  margin: 16px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

